<template>
  <div class="header">
    <span>
      <a-avatar
        :src="`${proxy}/cbh_file/${user.avatar}`"
      ></a-avatar>
    </span>
    <span style="padding: 0 12px; font-size: 16px;">{{ user.name }}</span>
    <span><a-icon type="logout" @click="logout" /></span>
  </div>
</template>
<script>
import { mapActions, mapState } from 'vuex'
export default {
  data() {
    return {
      proxy: process.env.VUE_APP_API_URL
    }
  },
  computed: {
    ...mapState('login', ['user'])
  },
  methods: {
    ...mapActions('login', {
      getAccount: 'getAccount'
    }),
    logout() {
      sessionStorage.clear()
      this.$router.push({
        name: 'Login'
      })
    }
  },
  created() {
    if(!this.user.id) {
      this.getAccount(Number(sessionStorage.getItem('user_id')))
    }
  }
}
</script>
<style lang="scss" scoped>
.header {
  display: flex;
  align-content: center;
  justify-content: flex-end;
}
</style>